body p {
  margin: 0;
}

.lessonsModule {
  height: 100%;
}

.lessonContent {
  height: 100%;
  width: calc(100% - 330px);
  padding: 10px;
  overflow-y: auto;
  float: left;
  box-sizing: border-box;
}

.lessonCategoriesContainer {
  width: 300px;
  height: 100%;
  margin-right: 10px;
  overflow-y: auto;
  float: left;
  box-sizing: border-box;
  text-align: left;
  background: #fff;
}

.lessonCategoryBlock {
  max-width: 300px;
  min-height: 34px;
  background: #fff;
  margin-bottom: 1px;
  display: flex;
  flex-direction: column;
}

.lessonCategory--arrow {
  margin-top: 12px;
  margin-left: 15px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid rgb(0, 149, 255);
}

.lessonCategory {
  margin-bottom: 1px;
  background: #f0f0f0;
  max-width: 300px;
  min-height: 30px;
  padding: 2px 0;
  display: flex;
}

.lessonBlock {
  margin-bottom: 1px;
  min-height: 30px;
  background: #f8f8f8;
  padding: 2px 0;
  display: flex;
  justify-content: space-between;
}

.lessonBlock .lessonLocked {
  color: brown;
  font-weight: bold;
  padding: 0 25px 0 10px;
  line-height: 28px;
}

.lessonCategory p, .lessonBlock p {
  padding: 0 10px 0 10px;
  max-width: 190px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 28px;
}
